}

/* end of font-sizes */
/* original sizes */
[class^="i-btn"] {
  padding: 6px 12px;
}

[class^="i-btn"][class*="i-btn-lg"] {
  padding: 10px 16px;
  /* +4px +4px */
  line-height: 1.333333333333333;
  border-radius: 6px;
}

[class^="i-btn"][class*="i-btn-sm"], [class^="i-btn"][class*="i-btn-xs"] {
  padding: 5px 10px;
  /* -1px -6px */
  font-size: 0.65rem;
  line-height: 1.5;
  border-radius: 3px;
}

[class^="i-btn"][class*="i-btn-xs"] {
  padding: 1px 5px;
}

/* end of original sizes */
[class^="i-btn"], a[class^="i-other-btn"] {
  border-radius: 4px;
  position: relative;
  display: inline-block;
  text-align: center;
  width: auto;
  cursor: pointer;
  font-family: Arial, Helvetica;
  color: white;
  font-weight: 400;
  text-shadow: 0px -1px 0px rgba(0, 46, 103, 0.65);
  text-align: center;
  line-height: 1.42857;
  transition: all 0.25s ease-in-out;
  -webkit-transition: all 0.25s ease-in-out;
  height: auto;
  vertical-align: middle;
  border-width: 1px;
  border-style: solid;
}

[class^="i-btn"] + [class^="i-btn"], [class^="i-other-btn"] + [class^="i-other-btn"] {
  margin-left: 4px;
}

/* full width block */
[class^="i-btn"][class*="i-btn-block"] {
  width: 100%;
  display: block;
  margin: 0px;
}

/* icons are same size */
[class^="i-btn"][class*="icon-"][class*="left-side"] {
  position: relative;
  padding-left: 38px;
  /* padding-left must be the image size + some pixels */
}

[class^="i-btn"][class*="icon-"][class*="left-side"]:before {
  content: " ";
  background-repeat: no-repeat;
  background-position: center center;
  top: 0px;
  position: absolute;
  left: 10px;
  top: 50%;
  margin-top: -14px;
  width: 28px;
  height: 28px;
}

/* margin-top must be minus half of the image size and width and height have to be the size of the image */
[class^="i-btn"][class*="icon-"][class*="right-side"] {
  position: relative;
  padding-right: 38px;
}

[class^="i-btn"][class*="icon-"][class*="right-side"]:before {
  content: " ";
  background-repeat: no-repeat;
  background-position: center center;
  top: 0px;
  position: absolute;
  right: 5px;
  top: 50%;
  margin-top: -14px;
  width: 28px;
  height: 28px;
}

/* either set them as content or as a background-image if you have a sprite */
[class^="i-btn"][class*="icon-edit"]:before {
  content: "";
  background-image: url("./../uihp-icon-edit.png");
  /* background-image */
}

/* btn borders */
[class^="i-btn"][class*="i-btn-main"], [class^="i-other-btn"][class*="i-btn-main"] {
  border-color: #226dc8;
}

[class^="i-btn"][class*="i-btn-info"], [class^="i-other-btn"][class*="i-btn-info"] {
  border-color: #2c96b4;
}

[class^="i-btn"][class*="i-btn-success"], [class^="i-other-btn"][class*="i-btn-info"] {
  border-color: #4cae4c;
}

[class^="i-btn"][class*="i-btn-default"], [class^="i-other-btn"][class*="i-btn-default"] {
  text-shadow: 0px 0px 0px black;
  color: #333333;
}

/* end of btn borders */
/* sctructure button is untested ensure line-height on span is bigger than the largest image else you wil get unequal buttons, good system if you have 2-3 sets of icons of different sizes but same sizes common together makes 3 classes of line-heights, one ofr each and add the class to them 
<a class='other-btn'>
	<i><img src='/path/to/img/' /></i>
	<span>Button text here</span>
</a>
<button class='other-btn'>
	<i><img src="./path/to/img' /></i>
	<span>button text here </span>
</button>
*/
[class^="i-other-btn"] {
  padding: 0px 8px;
  position: relative;
  display: table;
}

[class^="i-other-btn"] span {
  padding: 0px 6px;
  display: table-cell;
  vertical-align: middle;
}

[class^="i-other-btn"] i {
  display: table-cell;
  vertical-align: middle;
}

[class^="i-other-btn"] img {
  display: block;
}

