.radio, .checkbox {
  display: block;
  vertical-align: middle;
}

.radio-inline, .checkbox-inline {
  display: inline-block;
  vertical-align: middle;
  cursor: pointer;
}

.radio-inline + .radio-inline, .checkbox-inline + .checkbox-inline, .radio-inline + .checkbox-inline, .checkbox-inline + .radio-inline {
  margin-left: 10px;
}

.radio label, .checkbox label {
  cursor: pointer;
}

input[type="checkbox"], input[type="radio"] {
  display: none;
}

input[type="checkbox"] + label {
  position: relative;
  padding-right: 0px;
  display: block;
  line-height: 20px;
  min-height: 20px;
  cursor: pointer;
  padding-left: 24px;
}

input[type="checkbox"] + label:before {
  content: " ";
  position: absolute;
  left: 0px;
  top: 50%;
  margin-top: -10px;
  width: 20px;
  height: 20px;
  vertical-align: middle;
  background-image: url("./../images/button-checkbox.png");
  background-repeat: no-repeat;
  background-position: left top;
  cursor: pointer;
}

input[type="checkbox"]:checked + label:before {
  background-position: -20px top;
}

input[type="radio"] + label {
  position: relative;
  padding-left: 0px;
  display: block;
  min-height: 20px;
  padding-left: 24px;
  cursor: pointer;
}

input[type="radio"] + label:before {
  content: " ";
  position: absolute;
  left: 0px;
  top: 50%;
  margin-top: -10px;
  width: 20px;
  height: 20px;
  vertical-align: middle;
  background-image: url("./../images/button-radio.png");
  background-repeat: no-repeat;
