/**
 * The layout/ folder contains everything that takes part in laying out the site or application. This folder could have stylesheets for the main parts of the site (header, footer, navigation, sidebar…), the grid system or even CSS styles for all the forms.
 */
/** 
 * For smaller components, there is the component/ folder. While layout/ is macro (defining the global wireframe), components/ is more focused on widgets. It contains all kind of specific modules like a slider, a loader, a widget, and basically anything along those lines. There are usually a lot of files in components/ since the whole site/application should be mostly composed of tiny modules.
 */
/** Alerts SCSS */
.alert-primary, .alert-success, .alert-warning, .alert-danger {
  padding: 1rem 2rem;
  border-width: 1px;
  border-style: solid;
  border-radius: 5px;
  position: relative;
}

.alert-primary *, .alert-success *, .alert-warning *, .alert-danger * {
  margin-bottom: 1rem;
}

.alert-primary *:last-child, .alert-success *:last-child, .alert-warning *:last-child, .alert-danger *:last-child {
  margin-bottom: 0;
}

.alert-primary {
  color: #007bff;
  border-color: #0062cc;
  background: #e6f2ff;
}

.alert-success {
  color: #28a745;
  border-color: #1e7e34;
  background: #eaf6ec;
}

.alert-warning {
  color: #a17e14;
  border-color: #745a0e;
  background: #f6f2e8;
}

.alert-danger {
  color: #dc3545;
  border-color: #bd2130;
  background: #fcebec;
}

